<template>
  <div id="pushnotif">
    <!-- begin breadcrumb -->
    <ol class="breadcrumb pull-right">
      <li><a href="javascript:;">Home</a></li>
      <li class="active">{{ page.title }}</li>
    </ol>
    <!-- end breadcrumb -->
    <!-- begin page-header -->
    <h1 class="page-header">{{ page.title }} <small>{{ page.subtitle }}</small></h1>
    <!-- end page-header -->

    <div class="row">
      <div class="col-12">
        <div class="panel panel-inverse" data-sortable-id="form-validation-2">
          <div class="panel-heading">
            <h4 class="panel-title">Push Notification Form</h4>
          </div>
          <div class="panel-body panel-form">
            <form class="form-horizontal form-bordered" v-on:submit.prevent="pushNotification" data-parsley-validate="true">
              <div class="form-group">
                <label class="control-label col-2">Product</label>
                <div class="col-10">
                  <select class="form-control" name="product" v-model="product" data-parsley-required="true">
                    <option value="">---Select Product---</option>
                    <option v-for="product in products" v-bind:value="product.name" v-bind:key="product.name">{{ product.value }}</option>
                  </select>
                </div>
              </div>
              <div class="form-group">
                <label class="control-label col-2">Device</label>
                <div class="col-10">
                  <select class="form-control" name="device" v-model="device" data-parsley-required="true">
                    <option v-for="device in devices" v-bind:value="device.name" v-bind:key="device.name">{{ device.value }}</option>
                  </select>
                </div>
              </div>
              <div class="form-group">
                <label class="control-label col-2">To</label>
                <div class="col-10">
                  <select class="form-control" name="to" v-model="to">
                    <option v-for="to in send_to" v-bind:value="to.name" v-bind:key="to.name">{{ to.value }}</option>
                  </select>
                </div>
              </div>
              <div class="form-group">
                <label class="control-label col-2">Title</label>
                <div class="col-10">
                  <input type="text" class="form-control" name="title" v-model="title" data-parsley-required="true">
                  <small>{{ counTitle }}</small>
                </div>
              </div>
              <div class="form-group">
                <label class="control-label col-2">Body</label>
                <div class="col-10">
                  <textarea class="form-control" name="body" v-model="body" data-parsley-required="true" rows="5"></textarea>
                  <small>{{ countBody }}</small>
                </div>
              </div>
              <div class="form-group">
                <label class="control-label col-2">Image</label>
                <div class="col-10">
                  <input type="file" class="form-control">
                </div>
              </div>
              <div class="form-group">
                <label class="control-label col-2">Content</label>
                <div class="col-10">
                  <textarea class="summernote" name="content" v-model="content" v-on:change="value => { content = value }"></textarea>
                  <small>{{ countContent }}</small>
                </div>
              </div>
              <div class="form-group">
                <label class="control-label col-2">Action</label>
                <div class="col-10">
                  <input type="text" class="form-control" name="action" v-model="action">
                  <small>Contoh : "Insya Allah", "Siap!"</small>
                </div>
              </div>
              <div class="form-group">
                <label class="control-label col-2">Action URL</label>
                <div class="col-10">
                  <input type="text" class="form-control" name="action_url" v-model="action_url">
                </div>
              </div>
              <div class="form-group">
                <label class="control-label col-2"></label>
                <div class="col-10">
                  <button type="submit" class="btn btn-primary">Send Notifications</button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import fcm_to from "../../../static/fcm_send_to.json";

export default {
  name: 'pushnotif',
  data() {
    return {
      page: {
        title: 'Push Notifications',
        subtitle: 'remind everyone about the good deeds.',
      },
      devices: [{name: 'android', value: 'Android'}, {name: 'ios', value: 'iOS'}, {name: '', value: 'Both'}],
      products: [{name: 'yawme', value: 'Yawme'}, {name: 'tebi', value: 'Teman Bisnis'}, {name: 'cq', value: 'Complete Quran'}, {name: 'pa', value: 'Paytren Academy'}],
      product: '',
      to: '',
      device: '',
      title: '',
      body: '',
      content: '',
      package: '',
      action: '',
      action_url: '',
      fcm_key: {
        yawme: 'key=AAAAkWnkvNQ:APA91bE96jyAf49QRu9RUMoVVbPakaFGerXS3kzFguZOCJ5m_n3JVbyY1GGf9GeZGK9eufqD3aYMy8NAAwhtaWeX4QS9ejGGleI9BCvU-7Fp9RBSktfz9sDVSZznwh_1qvU6EpVlYXJL',
        tebi: '',
        cq: '',
        pa: '',
      },
      fcm_send_key: '',
      send_to: fcm_to,
    }
  },
  mounted() {
    var self = this;
    $(".select2").select2();
    $('.summernote').summernote({
        placeholder: 'Hi, this is summernote. Please, write text here! Super simple WYSIWYG editor on Bootstrap',
        height: 200,
        callbacks: {
          onKeydown: function(e) {
            var char = $(".note-editable").text();
            self.content = char;
          }
      }
    });
  },
  computed: {
    counTitle() {
      var char = this.title.length;

      return char + ' / ' + 'n';
    },
    countBody() {
      var char = this.body.length;

      return char + ' / ' + 'n';
    },
    countContent() {
      var char = this.content.length;

      return char + ' / ' + 'n';
    },
  },
  methods: {
    pushNotification() {
      var object = {
        to: this.to,
        priority: 'high',
        restricted_package_name: '',
        notification: {title: this.title, body: this.body, sound: 'default'},
        data: {title: this.title, message: this.body, image: '', content: ''},
        action: this.action,
        action_url: this.action_url,
      };
      // object.data.image = '';
      if (this.content !== '') {
        object.data.content = this.content;
      }
      this.fcm_send_key = this.fcm_key[this.product];
      if (this.device == 'android') {
        switch (this.product) {
          case 'yawme':
            this.package = 'id.co.badr.muslimassistant';
            break;
        }
        object.restricted_package_name = this.package;
        this.sendPush(object);
      } else if (this.device == 'ios') {
        switch (this.product) {
          case 'yawme':
            this.package = 'id.co.badr.yawme';
            break;
        }
        object.restricted_package_name = this.package;
        this.sendPush(object);
      } else {
        var object2 = object;
        switch (this.product) {
          case 'yawme':
            object.restricted_package_name = 'id.co.badr.muslimassistant';
            this.sendPush(object);
            object2.restricted_package_name = 'id.co.badr.yawme';
            this.sendPush(object2);
            break;
        }
      }
    },
    sendPush(data) {
      this.$http.post('https://fcm.googleapis.com/fcm/send', data, {
        headers: {
          'Authorization': this.fcm_send_key,
          'Content-Type': 'application/json'
        }
      })
      .then(response => {
        console.log(response);
      })
      .catch(error => {
        console.log(error);
      });
    }
  }
}
</script>



