<template>
    <!-- begin login -->
    <div id="login">
      <div class="login login-with-news-feed" v-if="!auth.user.authenticated">
        <!-- begin news-feed -->
        <div class="news-feed">
            <div class="news-image">
                <img src="~@/assets/img/Badr Remarkable.png" data-id="login-cover-image" alt="" />
            </div>
            <div class="news-caption quote-caption">
                <p>{{ quote.content }}</p>
                <span>{{ quote.title }}</span>
            </div>
        </div>
        <!-- end news-feed -->
        <!-- begin right-content -->
        <div class="right-content">
            <!-- begin login-header -->
            <div class="login-header">

            </div>
            <!-- end login-header -->
            <!-- begin login-content -->
            <div class="login-content">
                <div class="alert" :class="alertClass" role="alert">
                  {{ alert }}
                </div>
                <div class="text-center">
                  <img src="~@/assets/img/logo-badr-small.png" alt="" class="app-logo">
                  <br>
                  <br>
                  <div class="g-signin2" ref="signinBtn"></div>
                  <!-- <button class="btn btn-primary btn-social-icon btn-google" v-on:click="doLogin">
                    <span class="fa fa-google"></span> Sign in with Google
                  </button> -->
                </div>
                <hr />
                <p class="text-center">
                    &copy; Powered by <a href="http://badr.co.id">Badr Interactive</a>
                </p>
            </div>
            <!-- end login-content -->
        </div>
        <!-- end right-container -->
      </div>
      <!-- end login -->
    </div>
</template>
<script>
import auth from '@/services/auth.js'

export default {
  name: 'login',
  data () {
    return {
      quote: {
        title: 'The Holy Quran - 94:7',
        content: 'So when you have finished [your duties], then stand up [for worship].'
      },
      error: 0,
      alert: '',
      alertClass: '',
      auth: auth,
      google: {
        clientId: '392223742967-d1ha3fpu60289nlinkvntcp93aubke8a.apps.googleusercontent.com',
        clientSecret: 'GZXw-VHIx8l4gdov_Fi7U7A8',
      }
    }
  },
  head: {
    meta: function () {
      return [
        {name: 'google-signin-client_id', content: this.google.clientId}
      ]
    },
    script: [
      { type: 'text/javascript', src: 'https://apis.google.com/js/platform.js', async: true, defer: true},
    ],
  },
  methods: {
    doLogin (googleUser) {
      var profile = googleUser.getBasicProfile();
      var data = {
        google_token: googleUser.getAuthResponse().id_token,
      };

      this.$http.post('login', data)
                .then(response => {
                  console.log(response.data.success);
                  console.log(response.data.message);
                  if (response.data.success === true) {
                    auth.login({
                      access_token: 'Bearer abc123',
                      email: profile.getEmail(),
                      name: profile.getName(),
                    });
                  }
                })
                .catch(error => {
                  console.log(error.response);
                  console.error(error);
                });
    },
  },
  mounted () {
    if (!window.gapi) {
      console.error('"https://apis.google.com/js/api:client.js" needs to be included as a <script>.')
      return
    }

    if (!this.google.clientId) {
      console.error('google.clientId must be specified.')
      return
    }

    window.gapi.load('auth2', () => {
      const auth2 = window.gapi.auth2.init(this.google)
      // auth2.disconnect();
      auth2.attachClickHandler(this.$refs.signinBtn, {}, googleUser => {
        this.doLogin(googleUser)
      }, error => {
        console.error(error)
      })
    })
  }
}
</script>
<style lang="scss" scoped>
  @import url('./auth.scss');
  @import url('./login.scss');
</style>



