import Vue from 'vue'
import Router from 'vue-router'
import Universe from '@/components/page/Universe'
import Analytics from '@/components/page/Analytics'
import Apps from '@/components/page/Apps'
import Home from '@/components/page/Home'
import PushNotif from '@/components/page/PushNotif'
import Login from '@/components/auth/Login'
import auth from '@/services/auth.js'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: { auth: true }
    },
    {
      path: '/universe',
      name: 'Universe',
      component: Universe,
      meta: { auth: true }
    },
    {
      path: '/apps',
      name: 'Apps',
      component: Apps,
      meta: { auth: true }
    },
    {
      path: '/analytics',
      name: 'Analytics',
      component: Analytics,
      meta: { auth: true }
    },
    {
      path: '/pushnotif',
      name: 'PushNotif',
      component: PushNotif,
      meta: { auth: true }
    },
    {
      path: '/login',
      name: 'Login',
      components: { unauth: Login }
    },
  ],
  mode: 'history'
})

router.beforeEach((to, from, next) => {
  const auth = JSON.parse(localStorage.getItem('user'))
  if(to.meta.auth == true) {
    if(!auth || !auth.token) {
      next({name: 'Login'})
    }
  }
  next()
})

export default router



