<template>
  <div id="analytics">
    <!-- begin breadcrumb -->
    <ol class="breadcrumb pull-right">
      <li><a href="javascript:;">Home</a></li>
      <li class="active">{{ page.title }}</li>
    </ol>
    <!-- end breadcrumb -->
    <!-- begin page-header -->
    <h1 class="page-header">{{ page.title }} <small>{{ page.subtitle }}</small></h1>
    <!-- end page-header -->

    <div class="row">
      <!-- begin col-3 -->
      <div class="col-md-3 col-sm-6">
        <div class="widget widget-stats bg-green">
          <div class="stats-icon"><i class="fa fa-desktop"></i></div>
          <div class="stats-info">
            <h4>TOTAL VISITORS</h4>
            <p>3,291,922</p>
          </div>
          <div class="stats-link">
            <a href="javascript:;">View Detail <i class="fa fa-arrow-circle-o-right"></i></a>
          </div>
        </div>
      </div>
      <!-- end col-3 -->
      <!-- begin col-3 -->
      <div class="col-md-3 col-sm-6">
        <div class="widget widget-stats bg-blue">
          <div class="stats-icon"><i class="fa fa-chain-broken"></i></div>
          <div class="stats-info">
            <h4>BOUNCE RATE</h4>
            <p>20.44%</p>
          </div>
          <div class="stats-link">
            <a href="javascript:;">View Detail <i class="fa fa-arrow-circle-o-right"></i></a>
          </div>
        </div>
      </div>
      <!-- end col-3 -->
      <!-- begin col-3 -->
      <div class="col-md-3 col-sm-6">
        <div class="widget widget-stats bg-purple">
          <div class="stats-icon"><i class="fa fa-users"></i></div>
          <div class="stats-info">
            <h4>UNIQUE VISITORS</h4>
            <p>1,291,922</p>
          </div>
          <div class="stats-link">
            <a href="javascript:;">View Detail <i class="fa fa-arrow-circle-o-right"></i></a>
          </div>
        </div>
      </div>
      <!-- end col-3 -->
      <!-- begin col-3 -->
      <div class="col-md-3 col-sm-6">
        <div class="widget widget-stats bg-red">
          <div class="stats-icon"><i class="fa fa-clock-o"></i></div>
          <div class="stats-info">
            <h4>AVG TIME ON SITE</h4>
            <p>00:12:23</p>
          </div>
          <div class="stats-link">
            <a href="javascript:;">View Detail <i class="fa fa-arrow-circle-o-right"></i></a>
          </div>
        </div>
      </div>
      <!-- end col-3 -->
    </div>

    <div class="row">
      <!-- begin col-8 -->
      <div class="col-md-8">
        <div class="panel panel-inverse" data-sortable-id="index-1">
          <div class="panel-heading">
            <div class="panel-heading-btn">
              <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-default" data-click="panel-expand"><i class="fa fa-expand"></i></a>
              <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-success" data-click="panel-reload"><i class="fa fa-repeat"></i></a>
              <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-warning" data-click="panel-collapse"><i class="fa fa-minus"></i></a>
              <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-danger" data-click="panel-remove"><i class="fa fa-times"></i></a>
            </div>
            <h4 class="panel-title">Website Analytics (Last 7 Days)</h4>
          </div>
          <div class="panel-body">
            <div id="interactive-chart" class="height-sm"></div>
          </div>
        </div>
      </div>
      <!-- end col-8 -->
      <!-- begin col-4 -->
      <div class="col-md-4">
        <div class="panel panel-inverse" data-sortable-id="index-6">
          <div class="panel-heading">
            <div class="panel-heading-btn">
              <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-default" data-click="panel-expand"><i class="fa fa-expand"></i></a>
              <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-success" data-click="panel-reload"><i class="fa fa-repeat"></i></a>
              <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-warning" data-click="panel-collapse"><i class="fa fa-minus"></i></a>
              <a href="javascript:;" class="btn btn-xs btn-icon btn-circle btn-danger" data-click="panel-remove"><i class="fa fa-times"></i></a>
            </div>
            <h4 class="panel-title">Analytics Details</h4>
          </div>
          <div class="panel-body p-t-0">
            <table class="table table-valign-middle m-b-0">
              <thead>
                <tr>
                  <th>Source</th>
                  <th>Total</th>
                  <th>Trend</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td><label class="label label-danger">Unique Visitor</label></td>
                  <td>13,203 <span class="text-success"><i class="fa fa-arrow-up"></i></span></td>
                  <td><div id="sparkline-unique-visitor"></div></td>
                </tr>
                <tr>
                  <td><label class="label label-warning">Bounce Rate</label></td>
                  <td>28.2%</td>
                  <td><div id="sparkline-bounce-rate"></div></td>
                </tr>
                <tr>
                  <td><label class="label label-success">Total Page Views</label></td>
                  <td>1,230,030</td>
                  <td><div id="sparkline-total-page-views"></div></td>
                </tr>
                <tr>
                  <td><label class="label label-primary">Avg Time On Site</label></td>
                  <td>00:03:45</td>
                  <td><div id="sparkline-avg-time-on-site"></div></td>
                </tr>
                <tr>
                  <td><label class="label label-default">% New Visits</label></td>
                  <td>40.5%</td>
                  <td><div id="sparkline-new-visits"></div></td>
                </tr>
                <tr>
                  <td><label class="label label-inverse">Return Visitors</label></td>
                  <td>73.4%</td>
                  <td><div id="sparkline-return-visitors"></div></td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
      <!-- end col-4 -->
    </div>
  </div>
</template>
<script>
export default {
  name: 'analytics',
  data() {
    return {
      page: {
        title: 'Analytics',
        subtitle: 'growth of living things is always excited to see.',
      }
    }
  },
  mounted() {
    this.handleInteractiveChart();
  },
  methods: {
    handleInteractiveChart () {
      "use strict";
        function showTooltip(x, y, contents) {
            $('<div id="tooltip" class="flot-tooltip">' + contents + '</div>').css( {
                top: y - 45,
                left: x - 55
            }).appendTo("body").fadeIn(200);
        }
        var data1 = [
            [1, 40], [2, 50], [3, 60], [4, 60], [5, 60], [6, 65], [7, 75], [8, 90], [9, 100], [10, 105],
            [11, 110], [12, 110], [13, 120], [14, 130], [15, 135],[16, 145], [17, 132], [18, 123], [19, 135], [20, 150]
        ];
        var data2 = [
            [1, 10],  [2, 6], [3, 10], [4, 12], [5, 18], [6, 20], [7, 25], [8, 23], [9, 24], [10, 25],
            [11, 18], [12, 30], [13, 25], [14, 25], [15, 30], [16, 27], [17, 20], [18, 18], [19, 31], [20, 23]
        ];
        var xLabel = [
            [1,''],[2,''],[3,'May&nbsp;15'],[4,''],[5,''],[6,'May&nbsp;19'],[7,''],[8,''],[9,'May&nbsp;22'],[10,''],
            [11,''],[12,'May&nbsp;25'],[13,''],[14,''],[15,'May&nbsp;28'],[16,''],[17,''],[18,'May&nbsp;31'],[19,''],[20,'']
        ];
        $.plot($("#interactive-chart"), [
                {
                    data: data1,
                    label: "Page Views",
                    color: '#09f',
                    lines: { show: true, fill:false, lineWidth: 2 },
                    points: { show: true, radius: 3, fillColor: '#fff' },
                    shadowSize: 0
                }, {
                    data: data2,
                    label: 'Visitors',
                    color: '#00f',
                    lines: { show: true, fill:false, lineWidth: 2 },
                    points: { show: true, radius: 3, fillColor: '#fff' },
                    shadowSize: 0
                }
            ],
            {
                xaxis: {  ticks:xLabel, tickDecimals: 0, tickColor: '#ddd' },
                yaxis: {  ticks: 10, tickColor: '#ddd', min: 0, max: 200 },
                grid: {
                    hoverable: true,
                    clickable: true,
                    tickColor: "#ddd",
                    borderWidth: 1,
                    backgroundColor: '#fff',
                    borderColor: '#ddd'
                },
                legend: {
                    labelBoxBorderColor: '#ddd',
                    margin: 10,
                    noColumns: 1,
                    show: true
                }
            }
        );
        var previousPoint = null;
        $("#interactive-chart").bind("plothover", function (event, pos, item) {
            $("#x").text(pos.x.toFixed(2));
            $("#y").text(pos.y.toFixed(2));
            if (item) {
                if (previousPoint !== item.dataIndex) {
                    previousPoint = item.dataIndex;
                    $("#tooltip").remove();
                    var y = item.datapoint[1].toFixed(2);

                    var content = item.series.label + " " + y;
                    showTooltip(item.pageX, item.pageY, content);
                }
            } else {
                $("#tooltip").remove();
                previousPoint = null;
            }
            event.preventDefault();
        });
    }
  }
}
</script>



